import React from "react";
import styled from "@emotion/styled";
import { Link } from "gatsby";
// components
import { SectionHeading } from "../heading/sectionHeading";
import { ViewMoreBtn } from "../button/viewmoreBtn";
// utils
import { Color } from "../../utils/color";
import { ItemLists } from "../../utils/itemLists";

// sale === trueを抽出
const PickupItemLists = ItemLists.filter((Item) => {
  return Item.pickup === true;
});

export const PickupSection = () => {
  return (
    <SSection>
      <SContainer>
        <SectionHeading txt="ピックアップ中の製品" />
        <SWrapper>
          {PickupItemLists.map(({ id, name, price, brand, url, img }) => {
            return (
              <SItem key={id}>
                <Link to={url}>
                  <div>
                    <img src={img} alt="pickup" />
                  </div>
                  <div>
                    <span>{brand}</span>
                    <span>{name}</span>
                    <span>¥ {price}</span>
                  </div>
                </Link>
              </SItem>
            );
          })}
        </SWrapper>
        <SFooting>
          <ViewMoreBtn url="/pickup" />
        </SFooting>
      </SContainer>
    </SSection>
  );
};

const SSection = styled.section`
  padding: 4rem 0;
  border-bottom: 1px solid ${Color.gray100};
`;

const SContainer = styled.div`
  width: 96%;
  max-width: 1366px;
  margin: auto;
`;

const SWrapper = styled.div`
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(min(200px, 100%), 1fr));
  gap: 1rem;
`;

const SItem = styled.div`
  width: 100%;
  background-color: ${Color.white};
  /* 上から５件まで表示 */
  &:nth-of-type(n + 6) {
    display: none;
  }
  a {
    width: 100%;
    display: inline-flex;
    flex-direction: column;
    div {
      &:first-of-type {
        overflow: hidden;
        width: 100%;
        aspect-ratio: 1/1;
        img {
          transition: all 0.2s;
          width: 100%;
          height: 100%;
          object-fit: cover;
          object-position: center;
        }
      }
      &:last-of-type {
        padding: 1rem 0rem;
        display: inline-flex;
        flex-direction: column;
        span {
          color: ${Color.gray400};
          line-height: 1;
          &:nth-of-type(1) {
            font-size: 0.8rem;
          }
          &:nth-of-type(2) {
            margin-top: 0.5rem;
            font-size: 1rem;
            font-weight: 500;
          }
          &:nth-of-type(3) {
            margin-top: 1rem;
            font-size: 1rem;
            font-weight: 500;
          }
        }
      }
    }
    &:hover {
      div {
        img {
          transform: scale(1.2);
        }
      }
    }
  }
`;

const SFooting = styled.div`
  margin-top: 2rem;
`;
